<template>
  <div>
    <h1>新規Personの追加</h1>

    <form @submit.prevent="addPerson">
      <p>
        <label for="name">名前：</label>
        <input type="text" id="name" v-model="person.name" required>
      </p>

      <p>
        <label for="age">年齢(半角整数)：</label>
        <input type="number" id="age" v-model="person.age" required>
      </p>

      <p>
        <label for="gender">性別：</label>
        <select id="gender" v-model="person.gender" required>
          <option value="男">男性</option>
          <option value="女">女性</option>
        </select>
      </p>

      <p>
        <label for="occupation">職業：</label>
        <input type="text" id="occupation" v-model="person.occupation" required>
      </p>

      <p>
        <button type="submit">追加</button>
      </p>
    </form>

    <p>
      <button @click="goBack" style="margin-top: 10px;">一覧に戻る</button>
    </p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      person: {
        name: '',
        age: '',
        gender: '',
        occupation: ''
      }
    }
  },
  methods: {
    addPerson() {
      axios.post('http://localhost:8081/person/register', this.person)
          .then(response => {
            console.log(response.data);
            this.goBack();
          })
          .catch(error => {
            console.error(error);
          });
    },
    goBack() {
      this.$router.push('list');
    }
  }
}
</script>
