<template>
  <div>
    <h1>Person一覧</h1>
    <v-data-table
        :headers="headers"
        :items="persons"
        item-key="id">
      <template v-slot:items="props">
        <td>{{ props.item.id }}</td>
        <td>{{ props.item.name }}</td>
        <td>
          <v-btn @click="selectPerson(props.item.id)" color="primary" small>詳細</v-btn>
          <div style="display: flex;">
            <v-btn @click="selectPerson(props.item.id)" color="primary" small>詳細</v-btn>
            <v-btn @click="updatePerson(props.item.id)" color="info" small>更新</v-btn>
            <v-btn @click="deletePerson(props.item.id)" color="error" small>削除</v-btn>
          </div>
        </td>
      </template>
    </v-data-table>

    <p>{{ message }}</p>

    <v-btn @click="registerNew" color="success" style="margin-right: 10px;">新規追加</v-btn>
    <v-btn @click="returnToMenu" color="primary" style="margin-right: 10px;">メニューに戻る</v-btn>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      persons: [], // Person一覧を格納
      message: '', // 何かあればのメッセージを格納
      headers: [
        { text: 'id', value: 'id' },
        { text: '名前', value: 'name' },
        { text: '操作', value: 'actions', sortable: false },
      ],
    };
  },
  created() {
    axios.get('http://localhost:8081/person/list')
        .then(response => {
          this.persons = response.data;
        })
        .catch(error => {
          console.error(error);
        });
  },

  methods: {
    registerNew() {
      this.$router.push({ name: 'register' });
    },
    selectPerson(id) {
      this.$router.push({ name: 'person', params: { personId: id } });
    },
    updatePerson(id) {
      this.$router.push({ name: 'update', params: { personId: id } });
    },
    deletePerson(id) {
      this.$router.push({ name: 'delete', params: { personId: id } });
    },
    returnToMenu() {
      this.$router.push({ name: 'menu' });
    },
  },
};
</script>
