<template>
  <div>
    <h1>選択されたpersonの表示</h1>

    <p><strong>id：</strong> {{ person.id }}</p>
    <p><strong>名前：</strong> {{ person.name }}</p>
    <p><strong>年齢：</strong> {{ person.age }}</p>
    <p><strong>性別：</strong> {{ person.gender }}</p>
    <p><strong>職業：</strong> {{ person.occupation }}</p>

    <v-btn @click="goBack">一覧に戻る</v-btn>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      person: {
        id: '',
        name: '',
        age: '',
        gender: '',
        occupation: ''
      }
    }
  },
  methods: {
    goBack() {
      this.$router.push('/list');
    }
  },
  created() {
    axios.get(`http://localhost:8081/person/${this.$route.params.personId}`)
        .then(response => {
          this.person = response.data;
        })
        .catch(error => {
          console.error(error);
        });
  }
}
</script>
