<template>
  <div>
    <h1>Person更新</h1>
    <form @submit.prevent="submitForm">
      <p>各情報を更新してください。</p>
      <p>
        <label for="id">id(半角整数)：{{ person.id }}</label>
      </p>

      <p>
        <label htmlFor="name">名前：</label>
        <input type="text" id="name" v-model="person.name" required>
      </p>

      <p>
        <label htmlFor="age">年齢(半角整数)：</label>
        <input type="number" id="age" v-model="person.age" required>
      </p>

      <p>
        <label htmlFor="gender">性別：</label>
        <select id="gender" v-model="person.gender" required>
          <option value="男">男性</option>
          <option value="女">女性</option>
        </select>
      </p>

      <p>
        <label htmlFor="occupation">職業：</label>
        <input type="text" id="occupation" v-model="person.occupation" required>
      </p>

      <p>
        <v-btn type="submit">更新</v-btn>
      </p>
    </form>

    <p>
      <v-btn @click="goBack" style="wmargin-top: 10px;">一覧に戻る</v-btn>
    </p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      person: {
        id: '',
        name: '',
        age: '',
        gender: '',
        occupation: ''
      },
      persons: []
    }
  },
  methods: {
    submitForm() {
      axios.put('http://localhost:8081/person/update', this.person)
          .then(response => {
            console.log(response.data);
            this.goBack();
          })
          .catch(error => {
            console.error(error);
          });
    },
    goBack() {
      this.$router.push('/list');
    }
  },
  mounted() {
    axios.get(`http://localhost:8081/person/${this.$route.params.personId}`)
        .then((response) => {
          this.person = response.data;
        })
        .catch((error) => {
          console.error(error);
        });
  }
}
</script>
