<template>
  <div>
    <h1>選択されたPersonの削除</h1>
    <p>本当に削除しますか？</p>
    <v-btn @click="deletePerson" style="margin-right: 10px;">削除する</v-btn>
    <v-btn @click="goBack">リストに戻る</v-btn>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      id: ''
    };
  },
  methods: {
    deletePerson() {
      axios.delete('http://localhost:8081/person/delete/' + this.$route.params.personId)
          .then(() => {
            this.$router.push({ name: 'list' });
          })
          .catch(error => {
            console.error(error);
          });
    },
    goBack() {
      this.$router.push('/list');
    }
  },
}
</script>